import { EventEmitter, Injectable } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { BranchesArray } from "src/consts/branches.const";
import { IGeneralSetting } from "src/interfaces/i.general.setting.interface";
import { IUser } from "src/interfaces/i.user.interface";
import { AlertService } from "./alert.service";
import { CurrentUserService } from "./authentcation/current.user.service";
import { CustomValidatorsService } from "./custom.validators.service";
import { HttpService } from "./http/http.service";
import { SEOService } from "./seo.service";
import { SweetAlertService } from "./sweet.alert.service";

@Injectable()
export class FinancialTransferService {

    clinets: IUser[] = [];
    isFormSubmitError: boolean = false;
    isRequestingToServer: boolean = false;
    companies: IGeneralSetting[] = [];

    form: FormGroup = new FormGroup({});
    onDeletedItem: EventEmitter<string> = new EventEmitter<string>();
    isAllowEnterClinetManually: boolean;
    branches = BranchesArray;

    constructor(public u: CurrentUserService, public seo: SEOService, public router: Router, public http: HttpService, public sweetAlertService: SweetAlertService) { }
    /**
     * Init Create And Update Form
     */
    initForm() {
        this.form = new FormGroup({
            _id: new FormControl(),
            userId: new FormControl(null, [Validators.required]),
            notes: new FormControl(null),
            totalAmount_dollar: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            totalAmount_dinar: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            commissionTransfare: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            swift: new FormControl(null),
            refranceNumber: new FormControl(null),
            totalPaidAmount_dollar: new FormControl(null),
            totalPaidAmount_dinar: new FormControl(null),
            totalDueAmount_dollar: new FormControl(null),
            totalDueAmount_dinar: new FormControl(null),
            //لان ممكن ان يختار بان يدخل المستخدم يدويا
            clinetName: new FormControl(null),
            clinetPhoneNumber: new FormControl(null),
            branchIndex: new FormControl(null),
            dueDollarCommissionTransfare: new FormControl(0,Validators.required),
        });

        this.isFormSubmitError = false;
        this.isRequestingToServer = false;
        this.form.controls.totalAmount_dollar.valueChanges.subscribe(value =>
            this.form.controls.totalAmount_dinar.setValue(+value * +this.form.controls.commissionTransfare.value)
        );
        this.form.controls.commissionTransfare.valueChanges.subscribe(value =>
            this.form.controls.totalAmount_dinar.setValue(+value * +this.form.controls.totalAmount_dollar.value)
        );
        this.disapledControls();

        this.form.controls.userId.valueChanges.subscribe(value => {
            //Check If User Need To Enter Clinet Manually
            this.isAllowEnterClinetManually = value == "-1";
            if (this.isAllowEnterClinetManually) {
                this.form.controls.branchIndex.setValidators(Validators.required);
                this.form.controls.clinetName.setValidators(Validators.required);
                this.form.controls.clinetPhoneNumber.setValidators([CustomValidatorsService.phoneNumber, Validators.required]);
                this.form.controls.branchIndex.enable();
            } else {
                this.form.controls.branchIndex.clearValidators();
                this.form.controls.clinetName.clearValidators();
                this.form.controls.clinetPhoneNumber.clearValidators();
                this.form.controls.branchIndex.disable();
                //Select Branch Now
                if (value)
                this.form.controls.branchIndex.setValue(this.clinets.find(ce => ce._id == value).branchIndex);
            }
            this.form.controls.branchIndex.updateValueAndValidity();
            this.form.controls.clinetName.updateValueAndValidity();
            this.form.controls.clinetPhoneNumber.updateValueAndValidity();
        });
    }

    /**
     * Init Delete From
     */
    initDeletingForm() {
        this.form = new FormGroup({
            _id: new FormControl(),
            clinetName: new FormControl(null),
            refranceNumber: new FormControl(null),
        });
        this.form.disable();
    }

    /**
     * Init Payment Amount Form
     */
    initPaymentForm() {
        this.form = new FormGroup({
            _id: new FormControl(),
            clinetName: new FormControl(null),
            totalAmount_dollar: new FormControl(null),
            totalAmount_dinar: new FormControl(null),
            refranceNumber: new FormControl(null),
            totalPaidAmount_dollar: new FormControl(null),
            totalPaidAmount_dinar: new FormControl(null),
            totalDueAmount_dollar: new FormControl(null),
            totalDueAmount_dinar: new FormControl(null),
            paymentAmount_dollar: new FormControl(null, [Validators.required, CustomValidatorsService.invalidNumber]),
            paymentAmount_dinar: new FormControl(null, [Validators.required, CustomValidatorsService.invalidNumber]),
            paymentPlace: new FormControl(null, [Validators.required]),
            notes: new FormControl(null),
            dinarOnDueDollar: new FormControl(null)
        });

        this.isFormSubmitError = false;
        this.isRequestingToServer = false;
        this.form.controls.clinetName.disable();
        this.form.controls.totalAmount_dollar.disable();
        this.form.controls.totalAmount_dinar.disable();
        this.form.controls.refranceNumber.disable();
        this.form.controls.totalPaidAmount_dollar.disable();
        this.form.controls.totalPaidAmount_dinar.disable();
        this.form.controls.totalDueAmount_dollar.disable();
        this.form.controls.totalDueAmount_dinar.disable();
        this.form.controls.dinarOnDueDollar.disable();

    }

    /**
 * Close: Back To The Page
 */
    public closeModel() {
        this.isFormSubmitError = false;
        this.form.reset();
        this.router.navigate(['/financialTransfers']);
    }


    /** Disapled Som Form Controls */
    disapledControls() {
        this.form.controls.refranceNumber.disable();
        this.form.controls.totalAmount_dinar.disable();
        this.form.controls.swift.disable();
        this.form.controls.refranceNumber.disable();
        this.form.controls.totalPaidAmount_dollar.disable();
        this.form.controls.totalPaidAmount_dinar.disable();
        this.form.controls.totalDueAmount_dollar.disable();
        this.form.controls.totalDueAmount_dinar.disable();
        this.form.controls.branchIndex.disable();

    }

    /**Get Items To Select From Them */
    getItems(itemName?: string): void {
        let itemsNames = itemName ? [itemName] : [
            "clinets"
        ];
        this.http.getSelectItems<any>(itemsNames).subscribe(res => {
            if (res.isSuccess) {
                itemsNames.forEach(itemName => {
                    if (itemName == "clinets") {
                        this.clinets = [{
                            _id: "-1",
                            name: "<<إدخال زبون يدويا>>"
                        } as any, ...res.result[itemName]];
                    } else
                        this[itemName] = res.result[itemName];
                })
            } else {
                AlertService.showResponseMessage(res);
            }
        }, error => {
            AlertService.canRequestError(error);
        })
    }

    /**Reload Items */
    reloadItems(itemName: string): void {
        this.http.getSelectItems<any>(
            [itemName]).subscribe(res => {
                if (res.isSuccess) {
                    if (itemName == "clinets") {
                        this.clinets = [{
                            _id: "-1",
                            name: "<<إدخال زبون يدويا>>"
                        } as any, ...res.result[itemName]];
                    } else
                        this[itemName] = res.result[itemName];
                }
                AlertService.showResponseMessage(res);
            }, error => {
                AlertService.canRequestError(error);
            })
    }

}//End Class