import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { ITransaction } from "src/interfaces/i.transaction.interface";
import { FOPModel } from "src/models/fop/fop.model";
import { AlertService } from "src/services/alert.service";
import { CurrentUserService } from "src/services/authentcation/current.user.service";
import { CustomValidatorsService } from "src/services/custom.validators.service";
import { FinancialTransferService } from "src/services/financial.transfer.service";
import { HttpService } from "src/services/http/http.service";
import { SEOService } from "src/services/seo.service";
import { SharedComponents } from "../../shared.components";


@Component({
  selector: 'app-tinancial-transfer',
  templateUrl: './financial.transfers.component.html',
  styles: [
  ]
})
export class FinancialTransfersComponent extends SharedComponents implements OnInit {
  /**
   * Page Target
   */
  skip: number = 0;
  limit: number = 50;
  entries: ITransaction[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;
  formSubmitError: boolean;
  form: FormGroup = new FormGroup({
    userName: new FormControl(null),
    email: new FormControl(null),
    phoneNumber: new FormControl(null, CustomValidatorsService.phoneNumber),
    refranceNumber: new FormControl(null, CustomValidatorsService.invalidNumber),
    commissionTransfare: new FormControl(null, CustomValidatorsService.invalidNumber),
    createdDate: new FormControl(null),
  });
  constructor(public u: CurrentUserService, private seo: SEOService, public service: FinancialTransferService, private http: HttpService) { super() }

  ngOnInit(): void {
    this.seo.updateSEO("financialTransfers");
    this.getEntiries(false);

    //Listener On Delete Item Event
    this.allSubscriptions.push(
      this.service.onDeletedItem.subscribe(deletedItemId => {
        let deletedIndex = this.entries.findIndex(ent => ent._id == deletedItemId);
        if (deletedIndex >= 0) {
          this.entries.splice(deletedIndex, 1);
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
        }
      }));
  }


  /**
 * Get My Users
 */
  getEntiries(isReset): void {

    if (isReset) {
      if (this.form.invalid) {
        this.formSubmitError = true;
          this.fop = new FOPModel(this.fop.fieldNames, 0);
          return;
      }

      this.skip = 0;
      this.entries = [];
    }
    this.formSubmitError = false;
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.http.getFinancialTransfers<ITransaction[]>(this.skip, this.limit, this.form.value).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];

          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }


  funTrack(index: number, entitiy: ITransaction): string | undefined {
    return entitiy?._id;
  }
}//End CLass
