import { SharedComponents } from "src/app/shared.components";
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FinancialTransferService } from "src/services/financial.transfer.service";
import { AlertService } from "src/services/alert.service";
import { ITransaction } from "src/interfaces/i.transaction.interface";

@Component({
  selector: 'app-details-tinancial-transfer',
  templateUrl: './details.financial.transfer.component.html',
  styles: [
    `
.mt-container    {
      height: auto;
    }

    h6{
      display: inline-block;
      color:#1b55e2;
      font-weight: 700;
    font-size: 13px;
    letter-spacing: 1px;
    text-transform: uppercase;
    }

    h6:after{
      content:':'
    }
    `
  ]
})
export class DetailsFinancialTransferComponent extends SharedComponents implements OnInit {
  financialTransfe: ITransaction = {} as ITransaction;

  constructor(public service: FinancialTransferService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.seo.updateSEO("financialTransferDetils");
    //Get Detail From The Serer
    this.getFinancialTransferDetails();
  }

  /**
   * Get Details
   */
  getFinancialTransferDetails() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getFinancialTransferDetails<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.financialTransfe = res.result;
          } else {
            AlertService.showResponseMessage(res);
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }
}//End Class
