import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CreateFinancialTransferComponent } from "src/app/back-end/financial-transfers/create-financial-transfer/create.financial.transfer.component";
import { DeleteFinancialTransferComponent } from "src/app/back-end/financial-transfers/delete-financial-transfer/delete.financial.transfer.component";
import { DetailsFinancialTransferComponent } from "src/app/back-end/financial-transfers/details-financial-transfer/details.financial.transfer.component";
import { EditFinancialTransferComponent } from "src/app/back-end/financial-transfers/edit-financial-transfer/edit.financial.transfer.component";
import { FinancialTransfersComponent } from "src/app/back-end/financial-transfers/financial.transfers.component";
import { PayAmountFinancialTransferComponent } from "src/app/back-end/financial-transfers/pay-amount-financial-transfer/pay.amount.financial.transfer.component";

 
 
const ROUTS: Routes = [
    {
        path: '', component: FinancialTransfersComponent,
        children: [
            {path: "delete/:targetId",component: DeleteFinancialTransferComponent} ,
        ]
    },
    { path: 'create', component: CreateFinancialTransferComponent },
    { path: 'edit/:targetId', component: EditFinancialTransferComponent  },
    {path: "payAmount/:targetId",component: PayAmountFinancialTransferComponent} ,
    {path: "details/:targetId",component: DetailsFinancialTransferComponent} 
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class FinancialTransfersRoutingModule {

}

